'use client'

import { useState, useEffect } from 'react'
import { useWebSocket } from '@/hooks/useWebSocket'
import ChessBoard from '@/components/chess/ChessBoard'
import MatchHistory from '@/components/chess/MatchHistory'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Badge } from '@/components/ui/badge'
import { Alert, AlertDescription } from '@/components/ui/alert'

interface ActiveGame {
  id: string
  players: { white: string; black: string }
  currentTurn: 'white' | 'black'
  moveCount: number
  timeControl: string
  status: 'active' | 'finished'
}

interface GameState {
  [key: string]: string | null
}

interface Move {
  from: string
  to: string
  piece: string
  player: 'white' | 'black'
}

// Mock active games data
const mockActiveGames: ActiveGame[] = [
  {
    id: 'GAME001',
    players: { white: 'Alexandra Chen', black: 'Viktor Petrov' },
    currentTurn: 'black',
    moveCount: 24,
    timeControl: '15+10',
    status: 'active'
  },
  {
    id: 'GAME002',
    players: { white: 'Marcus Rodriguez', black: 'Lisa Wang' },
    currentTurn: 'white',
    moveCount: 18,
    timeControl: '10+5',
    status: 'active'
  },
  {
    id: 'GAME003',
    players: { white: 'David Thompson', black: 'Elena Kozlov' },
    currentTurn: 'black',
    moveCount: 31,
    timeControl: '30+15',
    status: 'active'
  },
  {
    id: 'GAME004',
    players: { white: 'James Park', black: 'Roberto Silva' },
    currentTurn: 'white',
    moveCount: 12,
    timeControl: '3+2',
    status: 'active'
  }
]

export default function Spectator() {
  const [selectedGameId, setSelectedGameId] = useState<string>('')
  const [customGameId, setCustomGameId] = useState<string>('')
  const [gameState, setGameState] = useState<GameState>({})
  const [currentTurn, setCurrentTurn] = useState<'white' | 'black'>('white')
  const [moves, setMoves] = useState<Move[]>([])
  const [isSpectating, setIsSpectating] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [gameInfo, setGameInfo] = useState<ActiveGame | null>(null)

  // WebSocket connection
  const wsUrl = typeof window !== 'undefined' ? 
    `ws://${window.location.hostname}:8000` : 
    'ws://localhost:8000'
  
  const { connected, lastMessage, sendMessage, connectionError } = useWebSocket(wsUrl)

  useEffect(() => {
    if (connectionError) {
      setError(connectionError)
    }
  }, [connectionError])

  useEffect(() => {
    if (lastMessage) {
      handleWebSocketMessage(lastMessage)
    }
  }, [lastMessage])

  const handleWebSocketMessage = (message: any) => {
    console.log('Spectator received message:', message)
    
    switch (message.type) {
      case 'spectate_started':
        setGameState(message.gameState || {})
        setCurrentTurn(message.currentTurn || 'white')
        setMoves(message.moves || [])
        setIsSpectating(true)
        setError(null)
        break
        
      case 'move_made':
        setGameState(message.gameState)
        setCurrentTurn(message.currentTurn)
        if (message.move) {
          setMoves(prev => [...prev, message.move])
        }
        break
        
      case 'game_ended':
        setError(`Game ended: ${message.result}`)
        break
        
      case 'error':
        setError(message.error || 'An error occurred')
        break
    }
  }

  const spectateGame = (gameId: string) => {
    if (!connected) {
      setError('Not connected to server')
      return
    }

    sendMessage({
      type: 'spectate',
      gameId: gameId
    })

    setSelectedGameId(gameId)
    const game = mockActiveGames.find(g => g.id === gameId)
    setGameInfo(game || null)
    setError(null)
  }

  const spectateCustomGame = () => {
    if (!customGameId.trim()) {
      setError('Please enter a game ID')
      return
    }
    spectateGame(customGameId.trim().toUpperCase())
  }

  const stopSpectating = () => {
    setIsSpectating(false)
    setSelectedGameId('')
    setCustomGameId('')
    setGameState({})
    setMoves([])
    setGameInfo(null)
    setError(null)
  }

  if (isSpectating) {
    return (
      <div className="container mx-auto px-4 py-8">
        <div className="grid lg:grid-cols-3 gap-8">
          {/* Chess Board */}
          <div className="lg:col-span-2">
            <ChessBoard
              gameState={gameState}
              currentTurn={currentTurn}
              playerColor={null} // Spectators don't have a color
              onMove={() => {}} // Spectators can't make moves
              disabled={true}
            />
            
            <div className="mt-6 text-center">
              <Button onClick={stopSpectating} variant="outline">
                Stop Spectating
              </Button>
            </div>
          </div>
          
          {/* Sidebar */}
          <div className="space-y-6">
            {/* Game Info */}
            <Card>
              <CardHeader>
                <CardTitle>Spectating Game</CardTitle>
              </CardHeader>
              <CardContent className="space-y-2">
                <div className="flex justify-between">
                  <span>Game ID:</span>
                  <span className="font-mono">{selectedGameId}</span>
                </div>
                {gameInfo && (
                  <>
                    <div className="flex justify-between">
                      <span>White:</span>
                      <span>{gameInfo.players.white}</span>
                    </div>
                    <div className="flex justify-between">
                      <span>Black:</span>
                      <span>{gameInfo.players.black}</span>
                    </div>
                    <div className="flex justify-between">
                      <span>Time Control:</span>
                      <span>{gameInfo.timeControl}</span>
                    </div>
                  </>
                )}
                <div className="flex justify-between">
                  <span>Current Turn:</span>
                  <span className="capitalize">{currentTurn}</span>
                </div>
                <div className="flex justify-between">
                  <span>Moves:</span>
                  <span>{moves.length}</span>
                </div>
              </CardContent>
            </Card>
            
            {/* Match History */}
            <MatchHistory moves={moves} gameId={selectedGameId} />
            
            {/* Error Display */}
            {error && (
              <Alert>
                <AlertDescription>{error}</AlertDescription>
              </Alert>
            )}
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="text-center mb-8">
        <h1 className="text-4xl font-bold mb-4">Spectator Mode</h1>
        <p className="text-xl text-muted-foreground max-w-2xl mx-auto">
          Watch live chess matches and learn from top players. 
          Select a game to spectate or enter a custom game ID.
        </p>
      </div>

      {!connected && (
        <Alert className="mb-6">
          <AlertDescription>
            {connectionError || 'Connecting to server...'}
          </AlertDescription>
        </Alert>
      )}

      <div className="grid lg:grid-cols-3 gap-8">
        {/* Active Games List */}
        <div className="lg:col-span-2">
          <Card>
            <CardHeader>
              <CardTitle>Live Games</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {mockActiveGames.map((game) => (
                  <div 
                    key={game.id}
                    className="flex items-center justify-between p-4 border rounded-lg hover:bg-muted/50 transition-colors"
                  >
                    <div className="flex-1">
                      <div className="flex items-center space-x-2 mb-2">
                        <span className="font-mono text-sm bg-muted px-2 py-1 rounded">
                          {game.id}
                        </span>
                        <Badge variant="outline">
                          {game.timeControl}
                        </Badge>
                        <Badge variant={game.status === 'active' ? 'default' : 'secondary'}>
                          {game.status}
                        </Badge>
                      </div>
                      <div className="text-sm">
                        <span className="font-semibold">{game.players.white}</span>
                        <span className="text-muted-foreground"> vs </span>
                        <span className="font-semibold">{game.players.black}</span>
                      </div>
                      <div className="text-xs text-muted-foreground mt-1">
                        Move {game.moveCount} • {game.currentTurn} to move
                      </div>
                    </div>
                    <Button 
                      onClick={() => spectateGame(game.id)}
                      disabled={!connected}
                      size="sm"
                    >
                      Watch
                    </Button>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Custom Game Input */}
        <div>
          <Card>
            <CardHeader>
              <CardTitle>Watch Custom Game</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="customGameId">Game ID</Label>
                <Input
                  id="customGameId"
                  value={customGameId}
                  onChange={(e) => setCustomGameId(e.target.value.toUpperCase())}
                  placeholder="Enter game ID"
                />
              </div>
              <Button 
                onClick={spectateCustomGame}
                disabled={!connected}
                className="w-full"
              >
                Spectate Game
              </Button>
              
              {error && (
                <Alert>
                  <AlertDescription>{error}</AlertDescription>
                </Alert>
              )}
            </CardContent>
          </Card>

          {/* Spectator Stats */}
          <Card className="mt-6">
            <CardHeader>
              <CardTitle>Spectator Stats</CardTitle>
            </CardHeader>
            <CardContent className="space-y-2">
              <div className="flex justify-between">
                <span>Active Games:</span>
                <span className="font-semibold">{mockActiveGames.length}</span>
              </div>
              <div className="flex justify-between">
                <span>Total Spectators:</span>
                <span className="font-semibold">1,247</span>
              </div>
              <div className="flex justify-between">
                <span>Games Today:</span>
                <span className="font-semibold">89</span>
              </div>
              <div className="flex justify-between">
                <span>Connection:</span>
                <span className={connected ? 'text-green-600' : 'text-red-600'}>
                  {connected ? 'Connected' : 'Disconnected'}
                </span>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  )
}
