'use client'

import { useEffect, useState } from 'react'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { ScrollArea } from '@/components/ui/scroll-area'
import { Button } from '@/components/ui/button'

interface Move {
  from: string
  to: string
  piece: string
  player: 'white' | 'black'
  timestamp?: number
}

interface MatchHistoryProps {
  moves: Move[]
  gameId?: string
}

export default function MatchHistory({ moves, gameId }: MatchHistoryProps) {
  const [savedMoves, setSavedMoves] = useState<Move[]>([])

  useEffect(() => {
    // Load saved moves from localStorage
    const storageKey = gameId ? `match_history_${gameId}` : 'current_match_history'
    const stored = localStorage.getItem(storageKey)
    if (stored) {
      try {
        setSavedMoves(JSON.parse(stored))
      } catch (error) {
        console.error('Error loading match history:', error)
      }
    }
  }, [gameId])

  useEffect(() => {
    // Save moves to localStorage whenever moves change
    if (moves.length > 0) {
      const storageKey = gameId ? `match_history_${gameId}` : 'current_match_history'
      localStorage.setItem(storageKey, JSON.stringify(moves))
      setSavedMoves(moves)
    }
  }, [moves, gameId])

  const clearHistory = () => {
    const storageKey = gameId ? `match_history_${gameId}` : 'current_match_history'
    localStorage.removeItem(storageKey)
    setSavedMoves([])
  }

  const formatMove = (move: Move, index: number) => {
    const moveNumber = Math.floor(index / 2) + 1
    const isWhiteMove = move.player === 'white'
    
    return {
      moveNumber,
      isWhiteMove,
      notation: `${move.from.toUpperCase()}-${move.to.toUpperCase()}`,
      piece: move.piece
    }
  }

  const displayMoves = moves.length > 0 ? moves : savedMoves

  return (
    <Card className="w-full">
      <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
        <CardTitle className="text-lg">Match History</CardTitle>
        {displayMoves.length > 0 && (
          <Button 
            variant="outline" 
            size="sm" 
            onClick={clearHistory}
            className="text-xs"
          >
            Clear
          </Button>
        )}
      </CardHeader>
      <CardContent>
        {displayMoves.length === 0 ? (
          <div className="text-center py-8">
            <p className="text-muted-foreground">No moves made yet</p>
            <p className="text-sm text-muted-foreground mt-1">
              Start playing to see move history
            </p>
          </div>
        ) : (
          <ScrollArea className="h-64">
            <div className="space-y-1">
              {displayMoves.map((move, index) => {
                const formatted = formatMove(move, index)
                
                return (
                  <div 
                    key={index}
                    className={`flex items-center justify-between p-2 rounded text-sm ${
                      formatted.isWhiteMove ? 'bg-muted/50' : 'bg-background'
                    }`}
                  >
                    <div className="flex items-center space-x-2">
                      <span className="font-mono text-xs text-muted-foreground w-6">
                        {formatted.isWhiteMove ? `${formatted.moveNumber}.` : ''}
                      </span>
                      <span className="text-lg">{formatted.piece}</span>
                      <span className="font-mono">{formatted.notation}</span>
                    </div>
                    <div className="flex items-center space-x-2">
                      <span className={`w-2 h-2 rounded-full ${
                        formatted.isWhiteMove ? 'bg-white border border-gray-300' : 'bg-black'
                      }`} />
                      <span className="text-xs text-muted-foreground capitalize">
                        {move.player}
                      </span>
                    </div>
                  </div>
                )
              })}
            </div>
          </ScrollArea>
        )}
        
        {displayMoves.length > 0 && (
          <div className="mt-4 pt-4 border-t">
            <div className="flex justify-between text-sm text-muted-foreground">
              <span>Total Moves: {displayMoves.length}</span>
              <span>
                Turn: {displayMoves.length % 2 === 0 ? 'White' : 'Black'}
              </span>
            </div>
          </div>
        )}
      </CardContent>
    </Card>
  )
}
